import React from 'react'

const BlogSingleCardLeft = ({id,img,headtext,desc,month,day,year,comment,author}) => {
  return (
    <>
    <div className="myblogcard myblogcardleft">
        {/* <img src={img} className="card-img-top" alt="..." /> */}
        <div className="card-body">
          <h5 className="card-title">{headtext}</h5>
          <div className="date">
            <p className='date-time'>{month} {day} ,{year} </p>
            <p className='comment'> {comment} comment </p>
            <p className='author'>{author}</p>
          </div>
          <p className="card-text desc">{desc.substring(0,90)}...<a href='#'>VIEW MORE</a> </p>
        </div>
      </div>
    </>
  )
}

export default BlogSingleCardLeft