import React from 'react'
import { BrowserRouter, Route, Routes } from 'react-router-dom'
import Home from '../pages/Home'
import Shop from '../pages/Shop'
import Blog from '../pages/Blog'
import Login from '../pages/Login'
import Admin from '../pages/Admin'
import Header from './Header'
import PageNotFound from '../pages/PageNotFound'
import Aboutproduct from './Aboutproduct'
// import BlogSingleCard from './BlogSingleCard'
import ShopAboutProduct from './ShopAboutProduct'
// import BlogAboutProduct from './BlogAboutProduct'
import BlogAboutProduct from './BlogAboutProduct'
import Footer from './Footer'
import { ProductProvider } from '../context/ProductContext'
import { CartProvider } from 'react-use-cart'
import Cart from './Cart'
const AppRouter = () => {
  // const [products] = useContext(ProductContext);
  return (
    <BrowserRouter>
    <CartProvider>
      <ProductProvider>
          <Header />
          <Routes>
            <Route path='*' element={<PageNotFound/>}></Route>
            <Route path='/' element={<Home/>}></Route>
            <Route path='/shop' element={<Shop/>}></Route>
            <Route path='/blog' element={<Blog/>}></Route>
            <Route path='/login' element={<Login/>}></Route>
            <Route path='/admin' element={<Admin/>}></Route>
            <Route path='/cart' element={<Cart/>}></Route>
            <Route path='/:url' element={<Aboutproduct/>}></Route>
            <Route path='/shop/:url' element={<ShopAboutProduct/>}></Route>
            <Route path='/blog/:url' element={<BlogAboutProduct/>}></Route>
            {/* <Route path='/blog/:url' element={<BlogAboutProduct/>}></Route> */}
          </Routes>
          <Footer />
      </ProductProvider>
    </CartProvider>
    </BrowserRouter>
  )
}

export default AppRouter



