import React from 'react'
import { useCart } from 'react-use-cart'

const Cart = () => {
    const {items,updateItemQuantity,removeItem}= useCart();
    const { cartTotal } = useCart();
    const { emptyCart } = useCart();
  return (
    <div>

        <div className="container">
        {items.length==0?<div style={{"width":"100%","height":"90vh","display":"flex","justify-content":"center","align-items":"center"}}><img style={{"width":"100%"}} src="https://cdn.dribbble.com/users/461802/screenshots/4421003/emptycart.gif" alt="" /></div>:
        
        <>
        <div className="top-part">
        <h2 style={{"textAlign":"center"}}>Cart List</h2>
    
    </div>
    
    <table className="table">
    <thead>
      <tr>
        <th scope="col">#</th>
        <th scope="col">Image</th>
        <th scope="col">Title</th>
        <th scope="col">Price</th>
        <th scope="col">Quantity</th>
        <th scope="col">Del</th>
      </tr>
    </thead>
    <tbody>
      {items.map((item,id)=>{
        return(
            <tr>
        <th scope="row">{id}</th>
        <td><img src={item.shown_img} width={60} alt="imgprops" /></td>
        <td>{item.name}</td>
        <td>${item.price.toFixed()*item.quantity}</td>
        <td><button className='btn btn-primary' onClick={() => updateItemQuantity(item.id, item.quantity - 1)}>-</button><span className='mx-2'>{item.quantity}</span> <button className='btn btn-primary' onClick={() => updateItemQuantity(item.id, item.quantity + 1)}>+</button></td>
        <td><button className='btn btn-danger' onClick={() => removeItem(item.id)}>X</button></td>
      </tr>
        )
      })}
    </tbody>
  </table>
    <div className="info d-flex" >
    <h5 className='mx-3' style={{"color":"#333333"}}>Total price: {cartTotal.toFixed()}$</h5>
    <button className='btn btn-primary' onClick={emptyCart}>Reset cart list</button>
    </div>
    </>
        }
        </div>
        
    </div>
  )
}

export default Cart