import React, { useContext } from 'react'
import { Col, Container, Row } from 'react-bootstrap';
import { LinkContainer } from 'react-router-bootstrap';
import { useParams } from 'react-router-dom'
import { ProductContext } from '../context/ProductContext';
// import mydata from '../data/data';
// import mydata2 from '../data/data2';
import BlogSingleCard from './BlogSingleCard';
import BlogSingleCardLeft from './BlogSingleCardLeft';
const BlogAboutProduct = () => {
    const {url} = useParams();
    const [products,setProducts]=useContext(ProductContext)
    const myproduct = products.blogprod.find(p=>p.id==url);
  return (
    <div className='blogaboutproduct'>
            
    {/* {myproduct===undefined ? <h1>loading...</h1>:
    <div className='aboutproduct'>

    <Container>
    <Row>
      <p>salam qaqa netersna</p>
    </Row>
    </Container>
    </div>
    } */}
  {/* {myproduct.map((item,index)=>{
    return(
      <h3>{item.dateDay}</h3>
    )
  })} */}
  <div className="hero">
        <div className="text-box">
        <h2>{myproduct.headtext}</h2>
        <p>

          <LinkContainer to='/'>
            <a>Home </a>
          </LinkContainer> 

           
          <LinkContainer to='/Blog'>
          <a>Blog</a>
          </LinkContainer>
          
           
           
          Clothes
        </p>
        </div>
  </div>
  <div className="news-part">
      <Container>
      <Row>
        <Col className='left-part d-none d-md-block ' xs={12} md={3} >
          <div className="categories">
            <h6 className="categorieshead">CATEGORIES</h6>
            <ul>
              <li><a href="#">Clothes</a></li>
              <li><a href="#">Fashion</a></li>
              <li><a href="#">News</a></li>
              <li><a href="#">Sale</a></li>
              <li><a href="#">Shorts</a></li>
            </ul>
          </div>
          <div className="recent-posts">
          <h6 className="recenthead">RECENT POSTS</h6>
          {products.blogprod.slice(0,2).map((item,index)=>{

              return(
                <BlogSingleCardLeft
                key={index} 
                // img={item.img}
                author = {item.author} 
                headtext={item.headtext}
                desc={item.desc}
                month={item.dateMonth}
                day={item.dateDay}
                year={item.dateYear}
                comment={item.comment}
                />

              )

          })}
          </div>
          <div className="tags">
          <p className="head">TAGS</p>
          <div className="tag-box">
            <p>Accessories</p>
          </div>
          <div className="tag-box">
            <p>Dresses</p>
          </div>
          <div className="tag-box">
            <p>Fashion</p>
          </div>
          <div className="tag-box">
            <p>Format</p>
          </div>
          <div className="tag-box">
            <p>Men</p>
          </div>
          <div className="tag-box">
            <p>Moutation</p>
          </div>
        </div>
        </Col>
        <div className="mycanvas mb-4 d-block d-md-none">
        <div>
        <button className="btn btn-success" type="button" data-bs-toggle="offcanvas" data-bs-target="#offcanvasExample" aria-controls="offcanvasExample">
          Categories
        </button>
        <div className="offcanvas offcanvas-start" tabIndex={-1} id="offcanvasExample" aria-labelledby="offcanvasExampleLabel">
          <div className="offcanvas-body">
          <div className="categories">
            <h6 className="categorieshead">CATEGORIES</h6>
            <ul>
              <li><a href="#">Clothes</a></li>
              <li><a href="#">Fashion</a></li>
              <li><a href="#">News</a></li>
              <li><a href="#">Sale</a></li>
              <li><a href="#">Shorts</a></li>
            </ul>
          </div>
          <div className="recent-posts">
          <h6 className="recenthead">RECENT POSTS</h6>
          {products.blogprod.slice(0,2).map((item,index)=>{

              return(
                <BlogSingleCardLeft
                key={index} 
                id={index+1}
                // img={item.img}
                author = {item.author} 
                headtext={item.headtext}
                desc={item.desc}
                month={item.dateMonth}
                day={item.dateDay}
                year={item.dateYear}
                comment={item.comment}
                />

              )

          })}
          </div>
          <div className="tags">
          <p className="head">TAGS</p>
          <div className="tag-box">
            <p>Accessories</p>
          </div>
          <div className="tag-box">
            <p>Dresses</p>
          </div>
          <div className="tag-box">
            <p>Fashion</p>
          </div>
          <div className="tag-box">
            <p>Format</p>
          </div>
          <div className="tag-box">
            <p>Men</p>
          </div>
          <div className="tag-box">
            <p>Moutation</p>
          </div>
        </div>
          </div>
        </div>
      </div>
        </div>
        <Col className='info-prod-part' xs={12} md={9} >
          <img className='prodimg' src={myproduct.img} alt="" />
          <h6 className='headtext'>{myproduct.headtext}</h6>
          <p className='desc'>{myproduct.desc}</p>
          <div className="comment-part">
            <div className="left-part">
            <p className='date'>{myproduct.dateMonth} {myproduct.dateDay}, {myproduct.dateYear} </p>
            <p className='comment-count'><i className="fa-solid fa-comment"></i> {myproduct.comment} Comment </p>
            <p className='author'><i className="fa-solid fa-user"></i> {myproduct.author}</p>
            </div>
            <div className="social-parts">
                <a href='#' className="circle facebook-color"><i className="fa-brands fa-facebook-f"></i></a>
                <a href='#' className="circle twitter-color"><i className="fa-brands fa-twitter"></i></a>
                <a href='#' className="circle linkedin-color"><i className="fa-brands fa-linkedin-in"></i></a>
                <a href='#' className="circle google-color"><i className="fa-brands fa-google-plus-g"></i></a>
            </div>
          </div>
          <LinkContainer to='/blog'>
          <button className='mt-4 btn btn-primary'> Back </button>
          </LinkContainer>
        </Col>
      </Row>
      </Container>

      </div>

    
    </div>
  )
}

export default BlogAboutProduct