import React from 'react'
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';
import { Navigation, Pagination, Scrollbar, A11y } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import { LinkContainer } from 'react-router-bootstrap';
// import mydata from '../data/mydata'
const FirstSingleCard = ({id,hiden_img,shown_img,name,price}) => {
  return (
    <>
    
        <LinkContainer to={`/${id}`}>
        <div className="myproduct-card">
          <div className="image-part">
            <img src={hiden_img} alt="hiddenimg" />
            <img src={shown_img} alt="shownimg" />
          </div>
          <div className="text-part">
            <p>{name}</p>
            <h6>${price}</h6>
          </div>
          <div className="hover-links">
            <a href="#" className="circle-link"><i className="fa-solid fa-cart-shopping"></i></a>
            <a href="#" className="circle-link"><i className="fa-solid fa-heart"></i></a>
            <LinkContainer to={`/${id}`}>
            <a className="circle-link"><i className="fa-solid fa-right-long"></i></a>
            </LinkContainer>
          </div>

        </div>
        </LinkContainer>
      
    </>
  )
}

export default FirstSingleCard