import React, { useState } from 'react';
import Offcanvas from 'react-bootstrap/Offcanvas';
import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import Form from 'react-bootstrap/Form';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import { LinkContainer } from 'react-router-bootstrap';
// import { ProductContext } from '../context/ProductContext';
// import {LinkContainer} from 'react-router-bootstrap'
const Header = () => {
    const [show, setShow] = useState(false);
    // const [products,setProducts] = useContext(ProductContext);
    

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
  
    const SearchFunc=()=>{
        if(document.querySelector('#search-button').className==='q mybtn'){
            document.querySelector('#search-input').className='d-block';
            document.querySelector('#search-button').className='x mybtn';
            document.querySelector('#search-button').innerHTML='<i class="fa-solid fa-x"></i>'
            document.querySelector('#wishlist').classList.remove('d-block');
            document.querySelector('#cart').classList.remove('d-block');  
            document.querySelector('#wishlist').classList.add('d-none');
            document.querySelector('#cart').classList.add('d-none');
        }
        else{
            document.querySelector('#search-button').className='q mybtn';
            document.querySelector('#search-button').innerHTML='<i className="fa-solid fa-magnifying-glass"></i>'
            document.querySelector('#search-input').className='d-none';  
            document.querySelector('#wishlist').classList.remove('d-none');
            document.querySelector('#cart').classList.remove('d-none');        
            document.querySelector('#wishlist').classList.add('d-block');
            document.querySelector('#cart').classList.add('d-block');
        }
    }
  return (
    <Navbar className='mynavbar' expand="lg">
      <Container fluid>
        <LinkContainer to='/'>
        <Navbar.Brand><img src="https://raw.githubusercontent.com/BaloghlanGasimov/oritinasite/main/img/header-logo.png" alt="" /></Navbar.Brand>
        </LinkContainer>
        <Navbar.Toggle aria-controls="navbarScroll" />
        <Navbar.Collapse id="navbarScroll">
          <Nav
            className="me-auto my-2 my-lg-0"
            style={{ maxHeight: '120px' }}
            navbarScroll
          >
            <LinkContainer to='/'>
            
            <Nav.Link>
            <div className="animate"></div>
                Home
            </Nav.Link>
            </LinkContainer>
            <LinkContainer to='/shop'>
            <Nav.Link>
            <div className="animate"></div>
                Shop
                {/* {products.length} */}
            </Nav.Link>
            </LinkContainer>
            <LinkContainer to='/blog'>
            <Nav.Link>
            <div className="animate"></div>
                Blog
            </Nav.Link>
            </LinkContainer>
          </Nav>
          {/* <Form className="d-flex">
            <Form.Control
              type="search"
              placeholder="Search"
              className="me-2"
              aria-label="Search"
            />
            <Button variant="outline-success">Search</Button>
          </Form> */}
          <LinkContainer to='/login'>
            <div className="admin">
              {/* <button>admin edit</button> */}
              <i className="fa-solid fa-lock-open"></i>
              <p>admin</p>
            </div>
            </LinkContainer>
          <div className="right-part">
            {/* <p>hello</p> */}
            
            <div id='wishlist' className="wishlist">
            <i className="fa-solid fa-heart"></i>
            <a href="#">WISHLIST</a>
            </div>
           <LinkContainer to='/cart'>
           <div id='cart' className="cart">
            <i className="fa-solid fa-bag-shopping"></i>
            <a href="#">CART</a>
            </div>
           </LinkContainer>
            <div className="search">
            <input id='search-input' type="search" className='d-none' placeholder='search'/>
            <button id='search-button' className='q mybtn'  onClick={SearchFunc}><i  className="fa-solid fa-magnifying-glass"></i></button>
            </div>
            <Button variant="" onClick={handleShow}>
            <i className="fa-solid fa-bars"></i>
      </Button>

      <Offcanvas show={show} onHide={handleClose}>
        <Offcanvas.Header closeButton>
          <Offcanvas.Title></Offcanvas.Title>
        </Offcanvas.Header>
        <Offcanvas.Body>
          {/* <div className="mycanvas-body"> */}
            <ul>
                <li><a href="#">MY ACCOUNT</a></li>
                <li><a href="#">CHECKOUT</a></li>
                <li><a href="#">CART</a></li>
                <li> <a href="#">WISHLIST</a></li>
            </ul>
            
            
            
           
          {/* </div> */}
        </Offcanvas.Body>
      </Offcanvas>
          </div>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  )
}

export default Header