import React from 'react'
import { LinkContainer } from 'react-router-bootstrap'

const BlogSingleCard = ({id,img,headtext,desc,month,day,year,comment,author}) => {
  return (
    <>
    <LinkContainer to={`/blog/${id}`}>
    <div className="col-12 col-sm-6 col-md-4 myblogcard">
        <img src={img} className="card-img-top" alt="..." />
        <div className="card-body">
          <h5 className="card-title">{headtext}</h5>
          <div className="date">
            <p className='date-time'>{month} {day} ,{year} </p>
            <p className='comment'> {comment} comment </p>
            <p className='author'>{author}</p>
          </div>
          <p className="card-text desc">{desc.substring(0,90)}...<a href={`/blog/${id}`}>VIEW MORE</a> </p>
        </div>
      </div>
    </LinkContainer>
      </>
  )
}

export default BlogSingleCard