import React, { useContext } from 'react'
import { Col, Container, Row } from 'react-bootstrap';
import { LinkContainer } from 'react-router-bootstrap';
import { useParams } from 'react-router-dom'
import { ProductContext } from '../context/ProductContext';
// import mydata from '../data/data';
// import mydata2 from '../data/data2';
const ShopAboutProduct = () => {
    const {url} = useParams();
    const [products,setProducts]=useContext(ProductContext)
    const myproduct = products.new_arrivals.find(p=>p.id==url);
  return (
    <>
            
    {myproduct===undefined ? <h1>loading...</h1>:
    <div className='aboutproduct'>

    <Container>
    <Row>
      <Col className="image-part" sm={12} md={6} >
      {/* <div className="image-part"> */}
      <img className='shownpic' src={myproduct.shown_img} alt="firstpic" />
      <img className='hidenpic' src={myproduct.hiden_img} alt="secondpic" />
    {/* </div> */}
      </Col>
      <Col sm={12} md={6} >
        <div className="text-box">
          <h4>{myproduct.name}</h4>
          <p>{myproduct.desc}</p>
          <div className="price-add">
          <h6>Price: ${myproduct.price}</h6>
          
          <button className='btn  addcartbtn'>Add to Cart</button>
          </div>
          

          <LinkContainer to='/shop'>
          <button className='btn  mt-5'>Go back</button>
          </LinkContainer>
        </div>
      </Col>
    </Row>
    </Container>
    </div>
    }


    
    </>
  )
}

export default ShopAboutProduct