import React, { useContext } from 'react'
// import { Swiper, SwiperSlide } from 'swiper/react';
// import 'swiper/css';
// import { useSwiper } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';
import { Navigation, Pagination, Scrollbar, A11y } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
// import mydata from '../data/mydata'
// import mydata from '../data/data';
import FirstSingleCard from './FirstSingleCard';
import { ProductContext } from '../context/ProductContext';
const CardSlider = ({aboutname,start,end}) => {
  // const swiper = useSwiper();
  const [products,setProducts] = useContext(ProductContext);
  // console.log(products);
  return (
    <div className='cardslider'>
        <div className="top-part">
            <div className="text-box">
                <p>DISCOVER OUR TRENDING PRODUCTS</p>
                <h3>{aboutname}</h3>
                {/* <p className="d-none">{key}</p> */}
            </div>
        </div>
        <div className="width-97 slider-parts">
        {/* <Swiper
      spaceBetween={50}
      slidesPerView={3}
      onSlideChange={() => console.log('slide change')}
      onSwiper={(swiper) => console.log(swiper)}
    >
      <SwiperSlide>Slide 1</SwiperSlide>
      <SwiperSlide>Slide 2</SwiperSlide>
      <SwiperSlide>Slide 3</SwiperSlide>
      <SwiperSlide>Slide 4</SwiperSlide>
      <SwiperSlide>Slide 4</SwiperSlide>
      <SwiperSlide>Slide 4</SwiperSlide>
      ...
    </Swiper>
    <button onClick={() => swiper.slideNext()}>Slide to the next slide</button> */}
     <Swiper
     
     breakpoints={{
      10: {
        slidesPerView: 2,
        spaceBetween: 20,
      },
      768: {
        slidesPerView: 4,
        spaceBetween: 40,
      },
      1024: {
        slidesPerView: 4,
        spaceBetween: 40,
      },
     }}
      // install Swiper modules
      modules={[Navigation, Pagination, Scrollbar, A11y]}
      spaceBetween={50}
      slidesPerView={2}
      navigation
      // pagination={{ clickable: true }}
      // scrollbar={{ draggable: true }}
      onSwiper={(swiper) => console.log(swiper)}
      onSlideChange={() => console.log('slide change')}
      
    >
      {products.new_arrivals.slice(start,end).map((item,index)=>{
        return(
            <SwiperSlide>
                <FirstSingleCard hiden_img={item.hiden_img} shown_img={item.shown_img} name={item.name} price={item.price} id={index+1} key={index+1} />
                </SwiperSlide>
        )
      })}
    </Swiper>
    
        </div>
        
    </div>
  )
}

export default CardSlider