import React from 'react'
import { LinkContainer } from 'react-router-bootstrap'
import { useParams } from 'react-router-dom'
import { useCart } from 'react-use-cart';

const ShopSingleProduct = ({alldata,id,hiden_img,shown_img,name,price}) => {
  const { addItem } = useCart();
  return (
    // const {url} = useParams();

    <>

        <div className="col-12 col-md-4  myproduct-card">
            <LinkContainer to={`/shop/${id}`}>
          <div className="image-part">
            <img src={hiden_img} alt="hiddenimg" />
            <img src={shown_img} alt="shownimg" />
          </div>
        </LinkContainer>
          <div className="text-part">
            <p>{name}</p>
            <h6>${price}</h6>
          </div>
          <div className="hover-links">
            <a href="#" onClick={() => addItem(alldata)} className="circle-link"><i className="fa-solid fa-cart-shopping"></i></a>
            <a href="#" className="circle-link"><i className="fa-solid fa-heart"></i></a>
            <LinkContainer to={`/shop/${id}`}>
            <a className="circle-link"><i className="fa-solid fa-right-long"></i></a>
            </LinkContainer>
          </div>

        </div>
    </>
  )
}

export default ShopSingleProduct