import React, { useContext } from 'react'
import { Col, Container, Row } from 'react-bootstrap'
import { LinkContainer } from 'react-router-bootstrap'
import BlogSingleCard from '../components/BlogSingleCard'
import BlogSingleCardLeft from '../components/BlogSingleCardLeft'
import { ProductContext } from '../context/ProductContext'
import mydata from '../data/data'
const Blog = () => {
  const [products,setProducts] = useContext(ProductContext)
  return (
    <section className='blog'>
      <div className="hero">
        <div className="text-box">
        <h2>NEWS</h2>
        <p>

          <LinkContainer to='/'>
            <a>Home </a>
          </LinkContainer> 

           
          News
          
        </p>
        </div>
      </div>

      <div className="news-part">
      <Container>
      <Row>
        <Col className='left-part d-none d-md-block ' xs={12} md={3} >
          <div className="categories">
            <h6 className="categorieshead">CATEGORIES</h6>
            <ul>
              <li><a href="#">Clothes</a></li>
              <li><a href="#">Fashion</a></li>
              <li><a href="#">News</a></li>
              <li><a href="#">Sale</a></li>
              <li><a href="#">Shorts</a></li>
            </ul>
          </div>
          <div className="recent-posts">
          <h6 className="recenthead">RECENT POSTS</h6>
          {products.blogprod.slice(0,2).map((item,index)=>{

              return(
                <BlogSingleCardLeft
                key={index} 
                // img={item.img}
                author = {item.author} 
                headtext={item.headtext}
                desc={item.desc}
                month={item.dateMonth}
                day={item.dateDay}
                year={item.dateYear}
                comment={item.comment}
                />

              )

          })}
          </div>
          <div className="tags">
          <p className="head">TAGS</p>
          <div className="tag-box">
            <p>Accessories</p>
          </div>
          <div className="tag-box">
            <p>Dresses</p>
          </div>
          <div className="tag-box">
            <p>Fashion</p>
          </div>
          <div className="tag-box">
            <p>Format</p>
          </div>
          <div className="tag-box">
            <p>Men</p>
          </div>
          <div className="tag-box">
            <p>Moutation</p>
          </div>
        </div>
        </Col>
        <div className="mycanvas mb-4 d-block d-md-none">
        <div>
        <button className="btn btn-success" type="button" data-bs-toggle="offcanvas" data-bs-target="#offcanvasExample" aria-controls="offcanvasExample">
          Categories
        </button>
        <div className="offcanvas offcanvas-start" tabIndex={-1} id="offcanvasExample" aria-labelledby="offcanvasExampleLabel">
          <div className="offcanvas-body">
          <div className="categories">
            <h6 className="categorieshead">CATEGORIES</h6>
            <ul>
              <li><a href="#">Clothes</a></li>
              <li><a href="#">Fashion</a></li>
              <li><a href="#">News</a></li>
              <li><a href="#">Sale</a></li>
              <li><a href="#">Shorts</a></li>
            </ul>
          </div>
          <div className="recent-posts">
          <h6 className="recenthead">RECENT POSTS</h6>
          {products.blogprod.slice(0,2).map((item,index)=>{

              return(
                <BlogSingleCardLeft
                key={index} 
                id={index+1}
                // img={item.img}
                author = {item.author} 
                headtext={item.headtext}
                desc={item.desc}
                month={item.dateMonth}
                day={item.dateDay}
                year={item.dateYear}
                comment={item.comment}
                />

              )

          })}
          </div>
          <div className="tags">
          <p className="head">TAGS</p>
          <div className="tag-box">
            <p>Accessories</p>
          </div>
          <div className="tag-box">
            <p>Dresses</p>
          </div>
          <div className="tag-box">
            <p>Fashion</p>
          </div>
          <div className="tag-box">
            <p>Format</p>
          </div>
          <div className="tag-box">
            <p>Men</p>
          </div>
          <div className="tag-box">
            <p>Moutation</p>
          </div>
        </div>
          </div>
        </div>
      </div>
        </div>
        <Col className='info-prod-part' xs={12} md={9} >
          <div className="row g-4">
          {products.blogprod.map((item,index)=>{
            return(
              <BlogSingleCard
               id={index+1}
               key={index} 
               img={item.img}
               author = {item.author} 
               headtext={item.headtext}
               desc={item.desc}
               month={item.dateMonth}
               day={item.dateDay}
               year={item.dateYear}
               comment={item.comment}
               />

            )
          })}
          </div>
          <div className="bottom">
            <div className="circle">1</div>
            <div className="circle">2</div>
          </div>
        </Col>
      </Row>
      </Container>

      </div>
    </section>
  )
}

export default Blog